export default {
	props:{
		tab:{
			type:Array,
			default:function(){
				return {}
			}
		},
		currentIndex:{
			type:Number,
			default:0
		},
		distance:{
			type:Number,
			default:0
		}
	},
	data() {
		return {
			old:{
				scrollTop:0
			}
	    }
	},
	methods:{
		scroll(e){
			this.old.scrollTop = e.detail.scrollTop;
		},
		switchTab(i){
			this.$emit('tapTab',{i})
		}
	}
}
